.madeonline-header {
  position: sticky;
  top: 0px;
  background-color: rgba(255, 255, 255, 0.9294117647);
  z-index: 9;
}
.madeonline-header .main-menu-sidebar {
  display: none;
}

.desktop-menu {
  z-index: 9;
  height: 100px;
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.9294117647);
  position: relative;
}
.desktop-menu .header-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
}
.desktop-menu .header-wrapper .nav-wrapper {
  display: flex;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links {
  display: flex;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links .menu-item-has-children {
  position: relative;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item a {
  font-size: 26px;
  font-weight: 500;
  font-family: Nunito, Sans-serif;
  padding: 13px 20px;
  color: #000;
  transition: ease-in-out 0.3s;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item .dropdown-some {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 35px;
  left: 11px;
  background-color: #fff;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item .dropdown-some .menu-item {
  width: 200px;
  padding: 12px 0;
  transition: ease-in-out 0.3s;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item .dropdown-some .menu-item:hover {
  background-color: #f36c2a;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item .dropdown-some .menu-item:hover a {
  color: #fff;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item .dropdown-some .menu-item a {
  color: #000;
  font-size: 18px;
  line-height: 25px;
  padding: 12px 10px;
  margin-bottom: 10px;
  width: 100%;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item:hover .dropdown-some {
  visibility: visible;
  opacity: 1;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.menu-item:hover a {
  color: #f36c2a;
  transition: ease-in-out 0.3s;
}
.desktop-menu .header-wrapper .nav-wrapper .desk-links li.current_page_item a {
  color: #f36c2a;
}
.desktop-menu .header-wrapper .nav-wrapper .contact-div a {
  font-family: Nunito, Sans-serif;
  font-size: 30px;
  font-weight: 900;
  color: #fff;
  background-color: #f36c2a;
  border: 2px solid #f36c2a;
  border-radius: 25px;
  padding: 8px 30px;
}
.desktop-menu .header-wrapper .nav-wrapper .contact-div a:hover {
  border: 2px solid #f36c2a;
  color: #f36c2a;
  background-color: #fff;
}
.desktop-menu .header-wrapper .logo a img {
  width: 100px;
  height: 82px;
}

.waves {
  position: relative;
  width: 100%;
  height: 100px;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}
.waves .parallax > use {
  animation: move-forever 55s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.waves .parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.waves .parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.waves .parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.waves .parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}

