  font-weight: 900;
  position: absolute;
  opacity: 0;
  top: 190px;
  overflow: hidden;
  color: #f36c2a;
  -webkit-animation: rotateWord 10s linear infinite 0s;
  -ms-animation: rotateWord 10s linear infinite 0s;
  animation: rotateWord 10s linear infinite 0s;
}
.hero-section .hero-content .hero-title-below:nth-child(2) {
  -webkit-animation-delay: 2s;
  -ms-animation-delay: 2s;
  animation-delay: 2s;
  color: #f36c2a;
}
.hero-section .hero-content .hero-title-below:nth-child(3) {
