.waves {
  position: relative;
  width: 100%;
  height: 100px;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;

  .parallax>use {
    animation: move-forever 55s cubic-bezier(.55, .5, .45, .5) infinite;
  }

  .parallax>use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
  }

  .parallax>use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
  }

  .parallax>use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
  }

  .parallax>use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
  }

  @keyframes move-forever {
    0% {
      transform: translate3d(-90px, 0, 0);
    }

    100% {
      transform: translate3d(85px, 0, 0);
    }
  }
}

#page-footer {
  background-color: #F3F3F3;

  .footer-info {
    display: flex;
    width: 100%;
    padding: 50px 0px 30px 0px;

    .footer-left {
      width: 100%;
      height: 100%;

      .footer-logo {
        width: 100%;
        height: 100%;
        padding-top: 30px;

        img {
          width: 50%;
          display: block;
          margin-left: auto;
          margin-right: auto;
          text-align: center;
        }
      }



    }

    .footer-middle {
      .info-block {
        text-align: center;

        p {
          color: #000000;
          font-family: "Nunito", Sans-serif;
          font-size: 25px;
          font-weight: 800;
          margin-bottom: 20px;
        }

        .contact-block {
          text-align: left;

          p {
            font-family: "Nunito", Sans-serif;
            font-size: 20px;
            font-weight: 400;
            color: #54595F;
            margin-bottom: 10px;
          }

          a {
            font-family: "Nunito", Sans-serif;
            font-size: 20px;
            font-weight: 400;
            color: #54595F;

          }
        }
      }
    }

    .footer-right {
      .footer-socials {
        width: 100%;
        text-align: center;

        p {
          color: #000000;
          font-family: "Nunito", Sans-serif;
          font-size: 25px;
          font-weight: 800;
          margin-bottom: 20px;
          // text-align: center;

        }

        i {
          font-size: 25px;
        }
      }
    }
  }

  .page-end {
    background-color: #F36833;

    p {
      text-align: center;
      color: #FFFFFF;
      font-family: "Nunito", Sans-serif;
      font-size: 20px;
      font-weight: 600;
      padding: 10px;
    }

  }
}